<template>
<div>
  <ul ref="ul">
    <li v-for="(elem, index) in data" ref="li" @mouseover="ho = index" :style="{'--color': elem.color}"
        :class="{hover: ho === index && active !== index, active: active === index}">
      <a v-if="'img' in elem" @click="onClick($event, elem, index)">
        <img :src="elem.img" :alt="elem.alt">
        <span v-if="'text' in elem">{{elem.text}}</span>
      </a>
      <a v-else-if="'ion-icon' in elem" @click="onClick($event, elem, index)">
        <ion-icon :name="elem['ion-icon']"/>
        <span v-if="'text' in elem">{{elem.text}}</span>
      </a>
      <a v-else @click="onClick($event, elem, index)">
        <span v-if="'text' in elem">{{elem.text}}</span>
      </a>
    </li>
  </ul>
</div>
</template>

<script>
// import * as ioniconContent from "ionicons";

export default {
  name: "glmVNav",
  props: {
    data: {
      type: Array,
      required: true
    },
    hover: {
      type: Number,
      default: 0
    },
    color: {
      type: String,
      default: "black"
    },
    bg: {
      type: String,
      default: undefined
    },
    active: {
      type: Number,
      default: -1
    },
    size: {
      type: String,
      default: "2.5em"
    },
    opacity: {
      type: String,
      default: "1"
    }
  },
  emits: {
    onClick: null
  },
  data() {
    return {
      ho: this.hover
    }
  },
  mounted() {
    // this.start();
  },
  updated() {
    // this.start();
  },
  methods: {
    onClick(event, elem, index) {
      event.preventDefault();
      if ("onClick" in elem) {
        elem.onClick(event);
      }
      else {
        this.$emit("onClick", event, elem, index);
      }
    },
    h() {
      let ans = 0;
      this.$refs.li.forEach(li => {ans += li.offsetHeight});
      return ans;
    },
    w() {
      let ans = 0;
      for (let child of this.$refs.li[0].children[0].children) {
        ans += child.offsetWidth;
      }
      return ans + 60 + "px";
    }
  },
  computed: {
    colour() {
      return this.data[this.ho].color || "rgb(2, 255, 64)";
    },
    activeColour() {
      if (this.active === -1) return "transparent";
      return this.data[this.active].color || "rgb(2, 255, 64)";
    },
    activePos() {
      if (this.active === -1) return "0px";
      // return this.active * this.$refs.li[0].offsetWidth + 58 + "px";
      return this.h() / this.data.length * this.active + 15 + "px";
    }
  }
}
</script>

<style scoped lang="less">
* {box-sizing: border-box}

ul {
  //background-color: v-bind(bg);
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 25%);
  //width: v-bind(w());
  //height: v-bind(h());
  padding: 0;
  margin: 0;
  opacity: v-bind(opacity);
  //overflow: hidden;
  li {
    list-style: none;
    padding: 0;
    margin: 0;
    width: 100%;
    overflow: visible;
    position: relative;
    background-color: v-bind(bg);
    a {
      position: relative;
      color: v-bind(color);
      text-decoration: none;
      display: flex;
      //flex-direction: column-reverse;
      padding: 20px 30px;
      text-align: center;
      backdrop-filter: blur(15px);
      z-index: 10;
      align-items: center;
      ion-icon {
        font-size: v-bind(size);
        pointer-events: none;
        opacity: 25%;
        transition: 0.25s;
      }
      img {
        width: v-bind(size);
        height: v-bind(size);
        pointer-events: none;
        opacity: 25%;
        transition: 0.25s;
        filter: grayscale(1);
      }
      span {
        opacity: 50%;
        margin-top: auto;
        margin-bottom: auto;
        padding-left: 20px;
        transition: 0.25s;
      }
    }
  }
  li:hover a, li.active a {
    ion-icon {
      opacity: 1;
      //color: v-bind(colour);
      //margin-top: -15px;
    }
    img {
      opacity: 1;
      filter: grayscale(0);
      //margin-top: -15px;
    }
    span {
      opacity: 1;
    }
  }
  li.active a span, li.active a ion-icon {
    color: v-bind(activeColour);
    transition: 0.5s ease;
    transition-delay: 0.5s;
  }
  li:hover a span, li:hover a ion-icon {
    color: white;
  }
  li:first-child, li:first-child a {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }
  li:last-child, li:last-child a {
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  li::before, li::after {
    position: absolute;
    content: '';
    top: 30%;
    transform: translateX(-50%);
    height: 30px;
    width: 10px;
    border-radius: 10px;
    transition: 0.5s ease;
    transition-delay: 0s;
    background: var(--color);
    box-shadow: 0 0 5px var(--color), 0 0 15px var(--color), 0 0 30px var(--color), 0 0 60px var(--color);
  }
  li:hover::after, li:hover::before {
    width: 50%;
    height: 80%;
    border-radius: 30px;
    transition-delay: 0.3s;
    top: 10%;
  }
  li::before {
    //left: -5px;
    left: 0;
  }
  li:hover::before {
    //left: -0.2px;
    left: 25%;
  }
  li::after {
    //right: -5px;
    right: -10px;
  }
  li:hover::after {
    //right: 0.5px;
    right: -25%;
  }
}
