<template>
<div class="area"><slot/></div>
</template>

<script>
import {defineComponent} from 'vue'
export default defineComponent({
  name: "glmContentArea",
  props: {
    bg: {
      type: String,
      default: undefined
    },
    clear: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    beforeBg() {
      return this.clear ? "transparent" : "linear-gradient(to left, rgba(255, 255, 255, 15%), transparent)";
    }
  }
});
</script>

<style scoped lang="less">
.area {
  background-color: v-bind(bg);
  border-radius: 10px;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 25%);
  padding: 0;
  backdrop-filter: blur(15px);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.area::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  background: v-bind(beforeBg);
  transform: skewX(45deg) translateX(0);
}
</style>