<script>
import ArticlesNav from "@/components/articlesNav.vue";
import ArticleBlock from "@/components/articleBlock.vue";
import {getArticle} from "@/articles";

export default {
  name: "articleView",
  components: {ArticleBlock, ArticlesNav},
  inject: ["articleGroups"],
  data() {
    return {
      article: {}
    }
  },
  methods: {
    randomColor() {
      return `rgb(${Math.floor(Math.random() * 1000 % 200 + 56)}, ${Math.floor(Math.random() * 1000 % 200 + 56)}, ${Math.floor(Math.random() * 1000 % 200 + 56)})`
    }
  },
  computed: {
    articleGroupsNavData() {return Object.entries(this.articleGroups).map(([id, {title, color, img, "ion-icon": ionIcon}]) => ({
      text: title,
      color,
      ...(ionIcon ? {"ion-icon": ionIcon} : {}),
      ...(img ? {img} : {}),
      onClick: () => {
        this.$router.push(`/article/${id}`);
      },
      id
    }))},
    articlesNavData() {return Object.entries((this.articleGroups[this.$route.params.group] ?? {}).articles ?? {}).map(([id, elem]) => ({
      text: elem,
      color: this.randomColor(),
      onClick: () => {
        this.$router.push(`/article/${this.$route.params.group}/${id}`);
      },
      id
    }))}
  },
  mounted() {
    if (!(this.$route.params.group in this.articleGroups && this.$route.params.article in this.articleGroups[this.$route.params.group].articles)) {
      this.$router.push("/");
      return;
    }
    getArticle(this.$route.params.group, this.$route.params.article).then(article => this.article = article);
  }
}
</script>

<template>
<div id="container">
  <div id="navs">
    <articles-nav :data="articlesNavData" :active="this.$route.params.article"/>
    <articles-nav :data="articleGroupsNavData" :active="this.$route.params.group"/>
  </div>
  <article-block id="article" :article="article"/>
</div>
</template>

<style scoped>
@font-face {
  font-family: 'CascadiaCodeLight';
  src: url('@/assets/CascadiaCode_Light.otf');
}

#container {
  display: flex;
  padding: 10px;
  width: 97vw;
  justify-content: center;
}

#article {
  margin-left: 20px;
  width: 100%;
  min-width: 800px;
  max-width: 1200px;
}
</style>