<script>
import GlmVNav from "@/components/ui/glmVNav.vue";

export default {
  name: "articlesNav",
  components: {GlmVNav},
  props: {
    data: {
      type: Array,
      required: true
    },
    active: {
      type: String,
      required: true
    }
  }
}
</script>

<template>
  <glm-v-nav :data="data" bg="rgba(0, 0, 0, 0.4)" color="white" :active="this.data.map(({id}) => id).indexOf(active)" size="25px" class="nav"/>
</template>

<style scoped>
.nav {
  font-size: 20px;
  font-family: CascadiaCodeLight, serif;
  margin-bottom: 10px;
}
</style>